import { TemaAsesoria } from './tema-asesoria';

export class AsesoriaQuerySst {
  get token(): string {
    return this._token;
  }

  set token(value: string) {
    this._token = value;
  }

  get idAgendamientoUsuario(): number {
    return this._idAgendamientoUsuario;
  }

  set idAgendamientoUsuario(value: number) {
    this._idAgendamientoUsuario = value;
  }

  get dtmCita(): string {
    return this._dtmCita;
  }

  set dtmCita(value: string) {
    this._dtmCita = value;
  }

  get fecha(): string {
    return this._fecha;
  }

  set fecha(fecha: string) {
    this._fecha = fecha;
  }

  get hora(): string {
    return this._hora;
  }

  set hora(hora: string) {
    this._hora = hora;
  }

  get idTema(): number {
    return this._idTema;
  }

  set idTema(idTema: number) {
    this._idTema = idTema;
  }

  get nombreTema(): string {
    return this._nombreTema;
  }

  set nombreTema(nombreTema: string) {
    this._nombreTema = nombreTema;
  }

  get reconfirmar(): boolean {
    return this._reconfirmar;
  }

  set reconfirmar(reconfirmar: boolean) {
    this._reconfirmar = reconfirmar;
  }

  get isCancel(): boolean {
    return this._isCancel;
  }

  set isCancel(isCancel: boolean) {
    this._isCancel = isCancel;
  }

  get recordarPIN(): boolean {
    return this._recordarPIN;
  }

  set recordarPIN(recordarPIN: boolean) {
    this._recordarPIN = recordarPIN;
  }

  private _token: string;

  private _idAgendamientoUsuario: number;

  private _dtmCita: string;

  private _fecha: string;

  private _hora: string;

  private _idTema: number;

  private _nombreTema: string;

  private _reconfirmar: boolean;

  private _isCancel: boolean;

  private _recordarPIN: boolean;

  constructor(json: any) {
    this.token = json.strToken;
    this.idAgendamientoUsuario = json.Pk_ID_EV_Agendamiento_Usuario;
    this.dtmCita = json.Dtm_Cita;
    this.idTema = json.PK_ID_EVTemasAgendamiento;
    this.nombreTema = json.strDescripcionTemaEvento;

    this.fecha = this.dtmCita.substring(0, 10);
    this.hora = this.dtmCita.substring(11, 16);

    if (this.hora < '12:00') {
      this.hora += ' AM';
    } else if (this.hora < '13:00') {
      this.hora += ' M';
    } else {
      const hh = parseInt(this.hora.substring(0, 2)) - 12;
      const mm = this.hora.substring(3, 5);

      this.hora = `${hh}:${mm} PM`;
    }

    this.reconfirmar = false;
    this.isCancel = false;
    this.recordarPIN = false;
  }

  static parse(json: any[]) {
    return json.map((item: any) => {
      const asesoriaQuery = new AsesoriaQuerySst(item);

      return asesoriaQuery;
    });
  }

  toJson(): string {
    return JSON.stringify(this.toObject());
  }

  toObject() {
    return {
      id: 1,
      token: this.token,
      idTema: this.idTema,
      fecha: this.fecha,
    };
  }
}
