import { Component, OnInit } from '@angular/core';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import { ActionSheetController, AlertController } from '@ionic/angular';
import { Router } from '@angular/router';
import { Device } from '@ionic-native/device/ngx';

import { Subscription } from 'rxjs';

import { MONTHS_SHORT } from 'src/app/commons/locale';
import { alertCommunicationError } from 'src/app/commons/user-feedback';
import { Empresa } from 'src/app/entities/empresa';
import { Entity } from 'src/app/entities/entity';
import { Trabajador } from 'src/app/entities/trabajador';
import { AsesoriaQuerySst } from 'src/app/entities/asesoria-sst-query';
import { TemaAsesoria } from 'src/app/entities/tema-asesoria';

import { ContextService } from 'src/app/services/context.service';
import { AsesoriaSstService } from 'src/app/services/asesoria-sst.service';
import { UtilsService } from 'src/app/services/utils.service';
import { IResponse } from 'src/app/commons/types';

enum WHICH_DATE {
  MIN,
  MAX,
  NOW
}

@Component({
  selector: 'app-asesoria-sst-agendamiento-vivo',
  templateUrl: './asesoria-sst-agendamiento-historia.page.html',
  styleUrls: ['./asesoria-sst-agendamiento-historia.page.scss'],
})
export class AsesoriaSstAgendamientoHistoriaPage implements OnInit {
  empresa: Empresa;
  user: Trabajador;
  temasAsesoria: TemaAsesoria[];
  asesoriasQuerySst: AsesoriaQuerySst[];

  WHICH_DATE_ENUM = WHICH_DATE;
  formGroup1: FormGroup;
  compareEntities: any;
  fechaInicio: string = (new Date()).toISOString();
  fechaFin: string = (new Date()).toISOString();
  colorLabel: string;

  readonly MONTH_SHORT_NAMES = MONTHS_SHORT;
  private submitAttempt: boolean;
  private asesoriaSubscription: Subscription;
  private listaMaestrosSubscription: Subscription;

  constructor(
    private formBuilder: FormBuilder,
    private actionSheetCtrl: ActionSheetController,
    private context: ContextService,
    private asesoriaSstService: AsesoriaSstService,
    private alertCtrl: AlertController,
    private utils: UtilsService,
    private router: Router,
    private device: Device,
  ) {
    this.submitAttempt = false;
    this.asesoriasQuerySst = [];
    this.compareEntities = Entity.compare;

    this.formGroup1 = formBuilder.group({
      temaAsesoriaFormControl: ['', Validators.compose([Validators.required])],
      fechaInicioFormControl: ['', Validators.compose([Validators.required])],
      fechaFinFormControl: ['', Validators.compose([Validators.required])],
    });
  }

  ngOnInit(): void {
    const bodyClasses = document.body.classList;

    this.colorLabel = '#283a49';
    if (bodyClasses.length >= 1 && bodyClasses[0] === 'dark') {
      this.colorLabel = '#ffffff';
    }

    const getData = async (): Promise<void> => {
      await this.utils.presentLoading();
      this.asesoriasQuerySst = [];
      this.temasAsesoria = [];

      this.listaMaestrosSubscription = this.asesoriaSstService.getListaMaestros().subscribe(
        (response: any) => {
          this.temasAsesoria = TemaAsesoria.parse(response.listaTemaAgendamiento);

          const json = { PK_ID_EVTemasAgendamiento: 0, strDescripcionTemaEvento: '-- No Aplica --' };
          const temaAsesoria = new TemaAsesoria(json);

          this.temasAsesoria = [temaAsesoria, ...this.temasAsesoria];
          this.utils.dismissLoading();
        },
        (error: any) => {
          alertCommunicationError(this.alertCtrl);
          this.utils.dismissLoading();
        }
      );
    };

    this.context.get().then(c => {
      getData();

      this.empresa = c.empresa;
      this.user = c.trabajador;
      this.fechaInicio = '';
      this.fechaFin = '';
    });
  }

  isTemaAsesoriaInvalid(): boolean {
    return !this.formGroup1.controls.temaAsesoriaFormControl.valid
      && this.submitAttempt;
  }

  isFechaInicioInvalid(): boolean {
    return !this.formGroup1.controls.fechaInicioFormControl.valid
      && this.submitAttempt;
  }

  isFechaFinInvalid(): boolean {
    return !this.formGroup1.controls.fechaFinFormControl.valid
      && this.submitAttempt;
  }

  async consultar(): Promise<void> {
    //await this.utils.presentLoading();
    await this.getDataFromInputs();

    const temaSeleccionado = this.formGroup1.controls.temaAsesoriaFormControl.value as TemaAsesoria;
    let fechaInicioString = '';
    let fechaFinString = '';

    if (this.fechaInicio !== '') {
      let date = new Date(this.fechaInicio);
      let year = date.getFullYear();
      let month = (1 + date.getMonth()).toString().padStart(2, '0');
      let day = date.getDate().toString().padStart(2, '0');
      fechaInicioString = day + '/' + month + '/' + year;
    }

    if (this.fechaFin !== '') {
      let date = new Date(this.fechaFin);
      let year = date.getFullYear();
      let month = (1 + date.getMonth()).toString().padStart(2, '0');
      let day = date.getDate().toString().padStart(2, '0');
      fechaFinString = day + '/' + month + '/' + year;
    }

    const idTemaSeleccionado = temaSeleccionado.id ? temaSeleccionado.id : '';
    this.asesoriasQuerySst = [];
    this.asesoriaSubscription = this.asesoriaSstService.consultar(fechaInicioString, fechaFinString, this.user.id, this.empresa.documentoEmp, idTemaSeleccionado)
      .subscribe(
        (response: any) => {
          if (response[0].edRespuestaServicio.intRespuesta === 2) {
            this.alert('Atención', '', response[0].edRespuestaServicio.strRespuesta);
          }
          else {
            this.asesoriasQuerySst = AsesoriaQuerySst.parse(response);
          }

          this.utils.dismissLoading();
        },
        (error: any) => {
          alertCommunicationError(this.alertCtrl);
          this.utils.dismissLoading();
        }
      );
  }

  async cancelar(asesoriaQuery: AsesoriaQuerySst) {
    asesoriaQuery.isCancel = true;
    asesoriaQuery.recordarPIN = false;
    asesoriaQuery.reconfirmar = false;
    await this.confirmar('Cancelación', '', `Esta seguro que desea cancelar la agenda con fecha ${asesoriaQuery.fecha} ${asesoriaQuery.hora}?`, asesoriaQuery);
  }

  reagendar(asesoriaQuery: AsesoriaQuerySst) {
    this.router.navigate(['sst-advisory/live-scheduling', asesoriaQuery.toObject()]);
  }

  recordarPIN(asesoriaQuery: AsesoriaQuerySst) {
    asesoriaQuery.isCancel = false;
    asesoriaQuery.recordarPIN = true;
    asesoriaQuery.reconfirmar = false;
    this.confirmar('Recordar PIN', '', `Esta seguro que desea recordar el PIN de la agenda con fecha ${asesoriaQuery.fecha} ${asesoriaQuery.hora}?`, asesoriaQuery);
  }

  reconfirmar(asesoriaQuery: AsesoriaQuerySst) {
    asesoriaQuery.isCancel = false;
    asesoriaQuery.recordarPIN = false;
    asesoriaQuery.reconfirmar = true;
    this.confirmar('Reconfirmar', '', `Esta seguro que desea reconfirmar la agenda con fecha ${asesoriaQuery.fecha} ${asesoriaQuery.hora}?`, asesoriaQuery);
  }

  async alert(title: string, subTitle: string, message: string): Promise<HTMLIonAlertElement> {
    const alert = await this.alertCtrl.create({
      header: title,
      subHeader: subTitle,
      message,
      buttons: [
        { text: 'Aceptar', role: 'null' }
      ],
      backdropDismiss: true
    });

    await alert.present();

    return alert;
  }

  async confirmar(title: string, subTitle: string, message: string, asesoriaQuery: AsesoriaQuerySst): Promise<HTMLIonAlertElement> {
    const alert = await this.alertCtrl.create({
      header: title,
      subHeader: subTitle,
      message,
      buttons: [
        {
          text: 'Aceptar',
          role: 'cancel',
          handler: () => {
            if (asesoriaQuery.isCancel) {
              this.confirmarCancelar(asesoriaQuery);
            }
            else if (asesoriaQuery.recordarPIN) {
              this.confirmarRecordarPIN(asesoriaQuery);
            }
            else if (asesoriaQuery.reconfirmar) {
              this.confirmarReconfirmar(asesoriaQuery);
            }
          }
        },
        { text: 'Cancelar', role: 'null' }
      ],
      backdropDismiss: true
    });

    await alert.present();

    return alert;
  }

  async confirmarCancelar(asesoriaQuery: AsesoriaQuerySst) {
    await this.utils.presentLoading();
    // Llamar el servicio para cancelar
    this.asesoriaSstService.cancelar(asesoriaQuery.token, this.device.uuid).subscribe(
      (response: any) => {
        this.alert('Atención', '', response.edRespuestaServicio.strRespuesta);
        this.submitAttempt = false;
        this.utils.dismissLoading();

        if (response.edRespuestaServicio.intRespuesta === 1) {
          this.consultar();
        }
      },
      (error: any) => {
        alertCommunicationError(this.alertCtrl);
        this.utils.dismissLoading();
      }
    );
  }

  async confirmarRecordarPIN(asesoriaQuery: AsesoriaQuerySst) {
    await this.utils.presentLoading();
    // Llamar el servicio para cancelar
    this.asesoriaSstService.recordarPIN(asesoriaQuery.token).subscribe(
      (response: any) => {
        this.alert('Atención', '', response.edRespuestaServicio.strRespuesta);
        this.submitAttempt = false;
        this.utils.dismissLoading();
      },
      (error: any) => {
        alertCommunicationError(this.alertCtrl);
        this.utils.dismissLoading();
      }
    );
  }

  async confirmarReconfirmar(asesoriaQuery: AsesoriaQuerySst) {
    await this.utils.presentLoading();
    // Llamar el servicio para cancelar
    this.asesoriaSstService.reconfirmar(asesoriaQuery.token).subscribe(
      (response: any) => {
        this.alert('Atención', '', response.edRespuestaServicio.strRespuesta);
        this.submitAttempt = false;
        this.utils.dismissLoading();
      },
      (error: any) => {
        alertCommunicationError(this.alertCtrl);
        this.utils.dismissLoading();
      }
    );
  }

  async getDataFromInputs(): Promise<void> {
    const c = await this.context.get();
  }

  cleanInputs(): void {
    this.formGroup1.controls.temaAsesoriaFormControl.setValue('');
  }

  getDate(whichDate: WHICH_DATE): string {
    const date = new Date();
    switch (whichDate) {
      case WHICH_DATE.MIN:
        return date.getFullYear()
          + '-' + ('0' + (date.getMonth() + 1)).slice(-2)
          + '-' + ('0' + date.getDate()).slice(-2);
      case WHICH_DATE.MAX:
        return '' + (date.getFullYear() + 1);
    }
  }

}

